import { NextResponse } from "next/server";
import PDFDocument from "pdfkit";
import path from "path";
import { promises as fs } from "fs";
import {
  siteConfig,
  timelineData,
  projectsData,
  educationItems,
  certifications,
  nextSteps,
} from "@/lib/data";

function formatTimeline() {
  return timelineData
    .map((item) => {
      const achievements = item.achievements?.length
        ? "\n" + item.achievements.map((ach) => `• ${ach}`).join("\n")
        : "";
      return `${item.year} • ${item.title} (${item.subtitle})\n${item.description}${achievements}`;
    })
    .join("\n\n");
}

function formatProjects() {
  return projectsData
    .filter((project) => project.featured)
    .map(
      (project) =>
        `${project.title} — ${project.description}\nStack: ${project.technologies.join(", ")}`
    )
    .join("\n\n");
}

function formatEducation() {
  return educationItems
    .map((edu) => `${edu.course} — ${edu.institution} (${edu.period})\n${edu.description}`)
    .join("\n\n");
}

function formatCertifications() {
  return certifications
    .map((cert) => `${cert.year} • ${cert.title} — ${cert.institution}\n${cert.description}`)
    .join("\n\n");
}

function formatNextSteps() {
  return nextSteps
    .map((step) => `${step.title} [${step.status}]\n${step.description}`)
    .join("\n\n");
}

export async function GET() {
  const doc = new PDFDocument({ margin: 50, size: "A4", autoFirstPage: false });
  const chunks: Buffer[] = [];

  doc.on("data", (chunk) => chunks.push(chunk));

  const fontDir = path.join(process.cwd(), "public", "fonts");
  const regularFont = path.join(fontDir, "Inter-Regular.ttf");
  const semiBoldFont = path.join(fontDir, "Inter-SemiBold.ttf");

  await fs.access(regularFont);
  await fs.access(semiBoldFont);

  doc.registerFont("Inter", regularFont);
  doc.registerFont("Inter-Bold", semiBoldFont);

  doc.addPage();

  doc.font("Inter-Bold").fontSize(24).text(siteConfig.name);
  doc.moveDown(0.2);
  doc.fontSize(14).font("Inter").text(siteConfig.title);
  doc.moveDown(0.5);
  doc
    .fontSize(10)
    .text(`${siteConfig.email} • ${siteConfig.phone} • ${siteConfig.location}`)
    .moveDown(1);

  doc.font("Inter-Bold").fontSize(14).text("Resumo");
  doc.moveDown(0.2);
  doc.font("Inter").fontSize(11).text(siteConfig.subheadline);
  doc.moveDown(0.5);

  doc.font("Inter-Bold").fontSize(14).text("Disponibilidade");
  doc.moveDown(0.2);
  doc.font("Inter").fontSize(11).text(siteConfig.availability);
  doc.moveDown(0.8);

  doc.font("Inter-Bold").fontSize(14).text("Experiências Relevantes");
  doc.moveDown(0.2);
  doc.font("Inter").fontSize(11).text(formatTimeline(), { align: "justify" });
  doc.moveDown(0.8);

  doc.font("Inter-Bold").fontSize(14).text("Projetos em Destaque");
  doc.moveDown(0.2);
  doc.font("Inter").fontSize(11).text(formatProjects(), { align: "justify" });
  doc.moveDown(0.8);

  doc.font("Inter-Bold").fontSize(14).text("Formação Acadêmica");
  doc.moveDown(0.2);
  doc.font("Inter").fontSize(11).text(formatEducation(), { align: "justify" });
  doc.moveDown(0.8);

  doc.font("Inter-Bold").fontSize(14).text("Certificações");
  doc.moveDown(0.2);
  doc.font("Inter").fontSize(11).text(formatCertifications(), { align: "justify" });
  doc.moveDown(0.8);

  doc.font("Inter-Bold").fontSize(14).text("Próximos Passos");
  doc.moveDown(0.2);
  doc.font("Inter").fontSize(11).text(formatNextSteps(), { align: "justify" });

  doc.end();

  await new Promise((resolve) => doc.on("end", resolve));

  const pdfBuffer = Buffer.concat(chunks);
  return new NextResponse(pdfBuffer, {
    status: 200,
    headers: {
      "Content-Type": "application/pdf",
      "Content-Disposition": `attachment; filename="leandro-rocha-cv.pdf"`,
    },
  });
}
